import Image from "next/image"
import { MagnifyingGlassIcon, GlobeAltIcon,Bars3Icon, UserIcon } from "@heroicons/react/24/solid"
function Header(){
    return(
        <header className="sticky z-50 top-0 p-5 shadow grid grid-cols-3 bg-white">
            <div className=" relative flex item-center h-10">
                <Image src="/R.webp" layout="fill" alt={""} objectFit="contain" objectPosition="left"/>
            </div>
            <div className="flex items-center border-1 border-gray-400 p-2 rounded-full shadow-lg">
                <input className="flex-grow p-2" type="text" placeholder="Search Here" />
                <MagnifyingGlassIcon className=" hidden md:inline h-8 p-2 bg-red-400 rounded-full text-white"/>          
                </div>
                <div className="flex items-center justify-end space-x-2 text-gray-500">
                    <p className="hidden md:inline">Become A Host</p>
                    <GlobeAltIcon className="h-8"/>
                    <div className="flex items-center p-2 border-2 rounded-full space-x-2 m-2">
                        <Bars3Icon className="h-6" />
                        <UserIcon className="h-6"/>
                    </div>
                </div>
        </header>
    )
}
export default Header